// in src/users.js
import React from "react";
import {
  List,
  Edit,
  Datagrid,
  EmailField,
  TextField,
  Filter,
  //Create,
  TextInput,
  ReferenceInput,
  SelectInput,
  SimpleForm,
  LongTextInput,
  SimpleList,
  Responsive,
  // EditButton,
  // ShowButton,
  // ReferenceField,
  DisabledInput
} from "react-admin";

const PostFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput
      label="Nama"
      source="nasabahId"
      reference="nasabah"
      allowEmpty
    >
      <SelectInput optionText="nama" />
    </ReferenceInput>
  </Filter>
);

export const Customers = props => (
  <List title="All Customers" {...props} filters={<PostFilter />}>
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.nasabahId}
          secondaryText={record => record.nama}
          tertiaryText={record => record.hp}
          //new Date(record.published_at).toLocaleDateString()
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <TextField source="nama" />
          <TextField source="alamat" />
          <TextField source="hp" />
          <EmailField source="email" />
        </Datagrid>
      }
    />
  </List>
);
const PostTitle = ({ record }) => {
  return <span>Post {record ? `"${record.title}"` : ""}</span>;
};
export const PostEdit = props => (
  <Edit title={<PostTitle />} {...props}>
    <SimpleForm>
      <DisabledInput source="id" />
      <ReferenceInput label="Nama" source="nasabahId" reference="nasabah">
        <SelectInput optionText="nama" />
      </ReferenceInput>
      <LongTextInput source="alamat" />
      <TextInput source="hp" />
      <TextInput source="email" />
    </SimpleForm>
  </Edit>
);

